<template>
  <div class="countdown">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;style type="text/css"&gt;
.countdown{padding:0 6px;font-size:14px;text-align: center;}
.countdown p{font-size:15px}
.countdown p font{color:#ff0303}
.countdown input[type='text']{background-color:#fff;background-image:none;border-radius:4px;border:1px solid #dcdfe6;box-sizing:border-box;color:#606266;display:inline-block;font-size:inherit;height:36px;line-height:1;outline:none;padding:0 5px;width:76%}
.countdown input[type='button']{display:inline-block;line-height:1;white-space:nowrap;cursor:pointer;background:#66b1ff;border:1px solid #dcdfe6;border-color:#dcdfe6;color:#606266;text-align:center;box-sizing:border-box;outline:none;margin:0;transition:.1s;font-weight:500;padding:10px 7px;font-size:14px;border-radius:4px}
&lt;/style&gt;
&lt;div class="countdown"&gt;
    &lt;p&gt;距离&lt;font&gt;11-11购物狂欢节&lt;/font&gt;还有：&lt;/p&gt;    
    &lt;div id="demo"&gt;&lt;/div&gt;
&lt;/div&gt;
&lt;script type="text/javascript"&gt;
    import countdown from "cy_static/countdown";
    var demo = document.querySelector("#demo");
    countdown.run({
        time : 60 * 60 * 2,
        downText : "{1}时{2}分{3}秒",
        onChange : function(html) {
            demo.innerHTML = html;
        },
        callback : function() {
            demo.innerHTML = "倒计时结束";
        }
    });
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">countdown 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import countdown from "cy_static/countdown";
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
    countdown.run({
        time : 60 * 60,
        downText : "距离结束还有{1}时{2}分{3}秒",
        onChange : function(html) {
            console.log(html);
        },
        callback : function() {
            console.log("倒计时结束");
        }
    });
&lt;/script&gt;
</xmp >
  
      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">对象方法</h2>		
        <ul class="ul-props">
          <li>
            <p><a>run</a>(params : Object) : <a>Void</a></p>
            <p>输出倒计时HTML模版字符串；</p>
            <table class="table-props border">
              <tr>
                <th style="width: 20%;">参数名</th>
                <th style="width: 20%;">类型</th>
                <th style="width: 30%;">描述</th>
                <th style="width: 20%;">默认值</th>
                <th style="width: 10%;" class="text-c">必需</th>
              </tr>
              <tr>
                <td>time</td>
                <td class="atn">Number</td>
                <td>倒计时间秒</td>
                <td>0</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>downText</td>
                <td class="atn">String</td>
                <td>倒计文本字符串模板</td>
                <td>""</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>onChange</td>
                <td class="atn">Function</td>
                <td>改变触发事件</td>
                <td>function() {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>callback</td>
                <td class="atn">Function</td>
                <td>倒计完成事件</td>
                <td>function() {}</td>
                <td class="text-c">N</td>
              </tr>
            </table>
          </li>
          <li>
            <p><a>format</a>(str : String, arr : Array) : <a>String</a></p>
            <p>输出模版字符串；</p>
            <table class="table-props border">
              <tr>
                <th style="width: 20%;">参数名</th>
                <th style="width: 20%;">类型</th>
                <th style="width: 30%;">描述</th>
                <th style="width: 20%;">默认值</th>
                <th style="width: 10%;" class="text-c">必需</th>
              </tr>
              <tr>
                <td>str</td>
                <td class="atn">String</td>
                <td>"字符串模板"</td>
                <td>undefined</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>arr</td>
                <td class="atn">Array</td>
                <td>数组数据</td>
                <td>undefined</td>
                <td class="text-c">Y</td>
              </tr>
            </table>
          </li>
        </ul> 
      </div>
  
    </div> 
  </div>
</template>

<script>
import {trim} from "../../../assets/js/codeStyle";
export default {
  name: 'countdown',
  data () {
    return {
      msg: 'countdown',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  }, 
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  },
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>    
<style scoped>
  .cy_example .codeArea{height: 495px;}
  .cy_example .iframeBox{flex:10}
</style>


